import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';

import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const biography = ({id, this: love}) => (
    <Panel id={id}>
        <header> </header>
        <div className="titlesbiography"  onClick={() => love.goBack()}>
            <Div>
            <Icon24BrowserBack width={30} height={30} style={{display: "inline-block"}}/> Авторы
            </Div>
        </div>    
        {console.log(love.state.biography_array)}
        {love.state.biography_array.map(function(result, i) {
                            return (
                                <div key={i}>
                                <div className="allbooks">
                                    <img onClick={() => love.goAuthor(result["id"])} alt="Ошибка" src={result['cover']}/>
                                    <div onClick={() => love.goAuthor(result["id"])} className="content">
        <div onClick={() => love.goAuthor(result["id"])} className="contenttitle">{result['first_name']} {result['last_name']}</div>
                                    <div className="contentreyt"> </div>
                                    <div onClick={() => love.goAuthor(result["id"])} className="contentauthor">{result['activity']}</div>
                                    <div onClick={() => love.goAuthor(result["id"])} className="contentviews">
                                        Язык: {result['lang']}
                                    </div>
                                </div>
                                <div className="bottonfunctions" onClick={() => {love.setState({authortitle: result['first_name'], authorlast: result['last_name']}); love.openModalAuthor(result['id'])}}><Icon24MoreVertical/></div>
                        </div>
                        </div>
                            )
                    })}
        
    </Panel>
);
biography.propTypes = {
    id: PropTypes.string.isRequired,
    this: PropTypes.object.isRequired,
};
export default biography;