import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const top = ({id, this: love}) => (
    <Panel id={id}>
        <header> </header>
        <div className="titlesbiography" onClick={() => love.goBack()}>
            <Div>
            <Icon24BrowserBack width={30} height={30} style={{display: "inline-block"}}/> Топ книг
            </Div>
        </div>    
        {love.state.bookstop.map(function(result, i) {
                            return (
                            <div key={i}>
                                <div className="allbooks">
                                    <img onClick={() => love.goBook(result["id"], "top")} alt="обложка" src={result['cover']}/>
                                    <div onClick={() => love.goBook(result["id"], "top")} className="content">
                                    <div onClick={() => love.goBook(result["id"], "top")} className="contenttitle">{result['name']}</div>
                                    <div className="contentreyt">{result['rating']}</div>
                                    <div onClick={() => love.goBook(result["id"], "top")} className="contentauthor">{(result['authors']['0'].first_name)} {(result['authors']['0'].last_name)}</div>
                                    <div onClick={() => love.goBook(result["id"], "top")} className="contentviews">
                                        Просмотров: {result['views']}
                                    </div>
                                </div>
                                <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], formodal_id: result['id'], }); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
                        </div>
                        </div>
                            )
                    })}
    </Panel>
);
top.propTypes = {
    id: PropTypes.string.isRequired,
    this: PropTypes.object.isRequired,
};
export default top;