import React from 'react';
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
const profile = ({ id, this: love}) => (
    <Panel id={id}>
        <div className="profile">
        <div className="titlesbiography"  onClick={() => love.goBack()}>
            <Icon24BrowserBack width={30} height={30} style={{display: "inline-block"}}/> Назад

        </div>
        <br/>
        <br/>
        <div className="titless">
            Избранное
        </div>
        <br/>
            {love.state.booksfavorite_array.map(function(result, i) {
                            return (
                                <div key={i}>
                                <div className="allbooks">
                                    <img onClick={() => love.goBook(result["id"], "profile")} alt="обложка" src={result['cover']}/>
                                    <div onClick={() => love.goBook(result["id"], "profile")} className="content">
                                    <div onClick={() => love.goBook(result["id"], "profile")} className="contenttitle">{result['name']}</div>
                                    <div className="contentreyt">{result['rating']}</div>
                                    <div onClick={() => love.goBook(result["id"], "profile")} className="contentauthor">{(result['authors']['0'].first_name)} {(result['authors']['0'].last_name)}</div>
                                    <div onClick={() => love.goBook(result["id"], "profile")} className="contentviews">
                                        Просмотров: {result['views']}
                                    </div>
                                </div>
                                <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], formodal_id: result['id'], }); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
                        </div>
                        </div>
                            )
                    })}
                    <br/>
        <div className="titless">
            История
        </div>
            {love.state.bookshistory_array.map(function(result, i) {
                            return (
                                <div key={i}>
                                <div className="allbooks">
                                    <img onClick={() => love.goBook(result["id"], "profile")} alt="обложка" src={result['cover']}/>
                                    <div onClick={() => love.goBook(result["id"], "profile")} className="content">
                                    <div onClick={() => love.goBook(result["id"], "profile")} className="contenttitle">{result['name']}</div>
                                    <div className="contentreyt">{result['rating']}</div>
                                    <div onClick={() => love.goBook(result["id"], "profile")} className="contentauthor">{(result['authors']['0'].first_name)} {(result['authors']['0'].last_name)}</div>
                                    <div onClick={() => love.goBook(result["id"], "profile")} className="contentviews">
                                        Просмотров: {result['views']}
                                    </div>
                                </div>
                                <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], formodal_id: result['id'], }); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
                        </div>
                        </div>
                            )
                    })}
        </div>
    </Panel>
);

export default profile;